"use client"

import { useState, useEffect } from "react"
import { Activity, Zap, Battery, TrendingUp, CheckCircle, Cpu, Settings, Sun, Moon } from "lucide-react"
import { XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, AreaChart, Area } from "recharts"

export default function Home() {
  const [currentTime, setCurrentTime] = useState(new Date())
  const [selectedDevice, setSelectedDevice] = useState<number | null>(null)
  const [isDarkMode, setIsDarkMode] = useState(false)

  const [isClient, setIsClient] = useState(false)
  useEffect(() => {
    setIsClient(true)
  }, [])

  const COST_PER_KWH = 1699.53

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date())
    }, 1000)
    return () => clearInterval(timer)
  }, [])

  useEffect(() => {
    if (isDarkMode) {
      document.documentElement.classList.add("dark")
    } else {
      document.documentElement.classList.remove("dark")
    }
  }, [isDarkMode])

  const energyData = {
    totalConsumption: 24.5,
    currentUsage: 3.2,
    voltage: 230,  // Contoh ‚Äî ganti dengan data sensor nyata
    devices: [
      { id: 1, name: "AC Ruang Tengah", usage: 1.8, status: "active", icon: "‚ùÑÔ∏è" },
      { id: 2, name: "Lampu Utama", usage: 0.3, status: "active", icon: "üí°" },
      { id: 3, name: "Kulkas", usage: 0.5, status: "active", icon: "üßä" },
      { id: 4, name: "TV", usage: 0.2, status: "inactive", icon: "üì∫" },
      { id: 5, name: "Mesin Cuci", usage: 0.8, status: "active", icon: "üß∫" },
      { id: 6, name: "Komputer", usage: 0.6, status: "inactive", icon: "üíª" },
    ],
  }

  const calculatedCost = (energyData.totalConsumption * COST_PER_KWH).toFixed(2)

  const powerConsumptionData = [
    { time: "00:00", power: 1200, voltage: 220, current: 5.45 },
    { time: "02:00", power: 800, voltage: 220, current: 3.64 },
    { time: "04:00", power: 600, voltage: 220, current: 2.73 },
    { time: "06:00", power: 1500, voltage: 220, current: 6.82 },
    { time: "08:00", power: 2800, voltage: 220, current: 12.73 },
    { time: "10:00", power: 3500, voltage: 220, current: 15.91 },
    { time: "12:00", power: 4200, voltage: 220, current: 19.09 },
    { time: "14:00", power: 3800, voltage: 220, current: 17.27 },
    { time: "16:00", power: 4500, voltage: 220, current: 20.45 },
    { time: "18:00", power: 5200, voltage: 220, current: 23.64 },
    { time: "20:00", power: 4800, voltage: 220, current: 21.82 },
    { time: "22:00", power: 2200, voltage: 220, current: 10.0 },
  ]

  const backendInfo = {
    endpoints: [
      { method: "GET", path: "/api/energy", description: "Get current energy data" },
      { method: "WS", path: "/ws/energy", description: "WebSocket for real-time updates" },
    ],
    systemStatus: "online",
    lastUpdate: "2025-11-20 10:30:15",
    connectedDevices: 1,
  }

  // Fungsi untuk cek status tegangan berdasarkan opsi B (nominal 230 V)
  function getVoltageStatus(voltage: number) {
    if (voltage < 200) {
      return "critical"
    } else if (voltage < 207) {
      return "warning"
    } else if (voltage <= 253) {
      return "normal"
    } else {
      return "overvoltage"
    }
  }

  const voltage = energyData.voltage
  const voltageStatus = getVoltageStatus(voltage)

  // Warna teks / border berdasarkan status
  const getVoltageColor = (status: string) => {
    switch (status) {
      case "normal":
        return isDarkMode ? "text-green-400" : "text-green-600"
      case "warning":
        return isDarkMode ? "text-yellow-300" : "text-yellow-600"
      case "critical":
        return isDarkMode ? "text-red-400" : "text-red-600"
      case "overvoltage":
        return isDarkMode ? "text-orange-400" : "text-orange-600"
      default:
        return isDarkMode ? "text-gray-400" : "text-gray-600"
    }
  }

  return (
    <div className={`min-h-screen ${isDarkMode ? "dark bg-gray-900" : "bg-gradient-to-br from-blue-50 to-indigo-100"}`}>
      {/* Header */}
      <header className={`${isDarkMode ? "bg-gray-800 border-gray-700" : "bg-white border-gray-200"} shadow-sm border-b`}>
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="bg-blue-600 p-2 rounded-lg">
                <Zap className="h-6 w-6 text-white" />
              </div>
              <div>
                <h1 className={`text-xl font-bold ${isDarkMode ? "text-white" : "text-gray-900"}`}>
                  Monitoring Energi Listrik
                </h1>
                <p className={`text-sm ${isDarkMode ? "text-gray-400" : "text-gray-500"}`}>
                  Rumah Pintar - Real-time Monitoring
                </p>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              {/* Theme Toggle */}
              <button
                onClick={() => setIsDarkMode(!isDarkMode)}
                className={`p-2 rounded-lg transition-colors ${
                  isDarkMode
                    ? "bg-gray-700 text-yellow-400 hover:bg-gray-600"
                    : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                }`}
              >
                {isDarkMode ? <Sun className="h-5 w-5" /> : <Moon className="h-5 w-5" />}
              </button>
              <div className="text-right">
                <div className={`text-lg font-semibold ${isDarkMode ? "text-white" : "text-gray-900"}`}>
                  {isClient ? currentTime.toLocaleTimeString("id-ID") : ""}
                </div>
                <div className={`text-sm ${isDarkMode ? "text-gray-400" : "text-gray-500"}`}>
                  {isClient
                    ? currentTime.toLocaleDateString("id-ID", {
                        weekday: "long",
                        year: "numeric",
                        month: "long",
                        day: "numeric",
                      })
                    : ""}
                </div>
              </div>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {/* Total Penggunaan */}
          <div className={`${isDarkMode ? "bg-gray-800 border-gray-700" : "bg-white border-gray-200"} rounded-xl shadow-sm p-6 border`}>
            <div className="flex items-center justify-between">
              <div>
                <p className={`text-sm font-medium ${isDarkMode ? "text-gray-400" : "text-gray-600"}`}>Total Penggunaan</p>
                <p className={`text-2xl font-bold ${isDarkMode ? "text-white" : "text-gray-900"}`}>{energyData.totalConsumption} kWh</p>
              </div>
              <div className={`${isDarkMode ? "bg-blue-900" : "bg-blue-100"} p-3 rounded-full`}>
                <Activity className={`h-6 w-6 ${isDarkMode ? "text-blue-400" : "text-blue-600"}`} />
              </div>
            </div>
          </div>

          {/* Daya Saat Ini */}
          <div className={`${isDarkMode ? "bg-gray-800 border-gray-700" : "bg-white border-gray-200"} rounded-xl shadow-sm p-6 border`}>
            <div className="flex items-center justify-between">
              <div>
                <p className={`text-sm font-medium ${isDarkMode ? "text-gray-400" : "text-gray-600"}`}>Daya Saat Ini</p>
                <p className={`text-2xl font-bold ${isDarkMode ? "text-white" : "text-gray-900"}`}>{(energyData.currentUsage * energyData.voltage).toFixed(0)} W</p>
              </div>
              <div className={`${isDarkMode ? "bg-green-900" : "bg-green-100"} p-3 rounded-full`}>
                <Zap className={`h-6 w-6 ${isDarkMode ? "text-green-400" : "text-green-600"}`} />
              </div>
            </div>
          </div>

          {/* Tegangan dengan Status */}
          <div className={`${isDarkMode ? "bg-gray-800 border-gray-700" : "bg-white border-gray-200"} rounded-xl shadow-sm p-6 border`}>
            <div className="flex items-center justify-between">
              <div>
                <p className={`text-sm font-medium ${isDarkMode ? "text-gray-400" : "text-gray-600"}`}>Tegangan</p>
                <p className={`text-2xl font-bold ${getVoltageColor(voltageStatus)}`}>
                  {voltage} V
                </p>
              </div>
              <div className={`${isDarkMode ? "bg-yellow-900" : "bg-yellow-100"} p-3 rounded-full`}>
                <Battery className={`h-6 w-6 ${isDarkMode ? "text-yellow-400" : "text-yellow-600"}`} />
              </div>
            </div>
            <div className={`mt-2 text-xs ${isDarkMode ? "text-gray-400" : "text-gray-500"}`}>
              Status: <span className={getVoltageColor(voltageStatus)}>{voltageStatus.toUpperCase()}</span>
            </div>
          </div>

          {/* Biaya Bulan Ini */}
          <div className={`${isDarkMode ? "bg-gray-800 border-gray-700" : "bg-white border-gray-200"} rounded-xl shadow-sm p-6 border`}>
            <div className="flex items-center justify-between">
              <div>
                <p className={`text-sm font-medium ${isDarkMode ? "text-gray-400" : "text-gray-600"}`}>Biaya Bulan Ini</p>
                <p className={`text-2xl font-bold ${isDarkMode ? "text-white" : "text-gray-900"}`}>Rp {Number(calculatedCost).toLocaleString("id-ID")}</p>
              </div>
              <div className={`${isDarkMode ? "bg-purple-900" : "bg-purple-100"} p-3 rounded-full`}>
                <TrendingUp className={`h-6 w-6 ${isDarkMode ? "text-purple-400" : "text-purple-600"}`} />
              </div>
            </div>
            <div className={`mt-2 text-xs ${isDarkMode ? "text-gray-400" : "text-gray-500"}`}>
              Tarif: Rp {COST_PER_KWH.toLocaleString("id-ID")}/kWh
            </div>
          </div>
        </div>

        {/* ‚Ä¶ bagian grafik, devices, backend, tips tetap seperti sebelumnya ‚Ä¶ */}

      </main>
    </div>
  )
}

